:root {
  --main: hsl(193, 38%, 86%);
  --green: 150, 100%, 66%;
  --accent: hsl(var(--green));
  --bg-main: hsl(218, 23%, 16%);
  --bg-2nd: hsl(217, 19%, 24%);
  --bg-accent: hsl(217, 19%, 38%);
  --font-manrope: "Manrope", sans-serif;
  --font-weight-default: 800;
  --size-quote: 1.75rem;
}

*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

body {
  margin: 0;
  background-color: var(--bg-main);
  font-family: var(--font-manrope);
  font-weight: var(--font-weight-default);
  color: var(--main);
}

main {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 0.9375rem;
}

.card {
  position: relative;
  border-radius: 0.5rem;
  background-color: var(--bg-2nd);
  padding: 2.5rem 1.5rem;
  text-align: center;
}
@media (min-width: 40em) {
  .card {
    max-width: 28.125rem;
  }
}

.advice__title {
  text-transform: uppercase;
  margin: 0 0 1rem;
  font-size: 0.6875rem;
  letter-spacing: 3px;
  color: var(--accent);
}
@media (min-width: 40em) {
  .advice__title {
    margin: 0 0 0.5rem;
  }
}

.advice__quote {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  min-height: 20.625rem;
  font-size: var(--size-quote);
}
@media (min-width: 40em) {
  .advice__quote {
    min-height: 13.5rem;
  }
}

.advice__divider {
  margin: -0.5rem 0 0.5rem;
}
.advice__divider img {
  width: 100%;
}

.advice__dice {
  position: absolute;
  bottom: -3.125rem;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 100%;
  overflow: hidden;
  width: 50px;
  height: 50px;
  background-color: var(--accent);
}
.advice__dice img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.25rem;
  height: 1.25rem;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  margin-top: 1.5625rem;
}
.footer .attribution {
  font-size: 11px;
  text-align: center;
}
.footer .attribution a {
  color: var(--accent);
}

/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */
button {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

/**
 * Show the overflow in IE.
 * 1. Show the overflow in Edge.
 */
button {
  overflow: visible; /* 1 */
}

/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 * 1. Remove the inheritance of text transform in Firefox.
 */
button {
  text-transform: none; /* 1 */
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/* CUSTOM */
.btn {
  cursor: pointer;
  border: none;
}
.btn:hover {
  box-shadow: 0 0 1.25rem 0.5rem hsla(var(--green), 0.5);
}